<template>
	<view>
		<image :src="avatarUrl" mode=""></image>
		<view class="">
			{{nickName}}
		</view>
		<view class="button" @click="click">
			登录
		</view>
	</view>
</template>

<script> 
	export default {
		data() {
			return {
				nickName:'',
				avatarUrl:""
			}
		},
		methods: {
			click(){
				var that = this;
				uni.login({
				  provider: 'weixin',
				  success: function (loginRes) {
				    console.log(loginRes.authResult);
				    // 获取用户信息
				    uni.getUserInfo({
				      provider: 'weixin',
				      success: function (infoRes) {
						// console.log(infoRes)
						that.nickName = infoRes.userInfo.nickName;
						that.avatarUrl = infoRes.userInfo.avatarUrl;
				        console.log('用户昵称为：' + infoRes.userInfo.nickName);
				      }
				    });
				  }
				});
			}
		}
	}
</script>

<style scoped>
.button{
	padding: 20upx;
	background-color: red;
	width: 200upx;
	height: 50upx;
}
</style>
